@font-face {
  font-family: nunito;
  src: url(/static/media/Nunito-Light.abdadce6.ttf);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: nunito;
  /* background-image: url("images/backgroundmaybe.PNG"); */
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-backdrop-filter: blur(3px) opacity(0.8);
          backdrop-filter: blur(3px) opacity(0.8);
}

.container {
  width: 100%;
  height: 100vh;
  margin: auto;
  overflow: auto;
  min-height: 300px;
  
  border-radius: 5px;
  position: relative;
}

.content-wrapper{
  font-family: nunito;
}

.loginBox {
  text-align: center;
  position: absolute;
  /* border: 1px solid steelblue; */
  
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40rem;
  height: 33rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  background-color: white;
}
::-webkit-input-placeholder{
  color:rgb(168, 168, 168);
}
:-ms-input-placeholder{
  color:rgb(168, 168, 168);
}
::placeholder{
  color:rgb(168, 168, 168);
}
.login-container{
  /* margin-top: 15%; */
}
.signup-header{
  margin-bottom: 15%;

}
.signup-header h1{
  margin-top: 12%;
  font-size: 45px;
}
.signup-header h2{
  margin-top: 8px;
  display: inline;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.cred-container{
  margin-top: 4%;
  margin-bottom: 4%;
  margin-left: 3rem;
  margin-right: 3rem;
}
.cred-container form{
  margin-left: 3rem;
  margin-right: 3rem;
}
.btn {
  display: inline-block;
  background: #36A7F9;
  color: #fff;
  border: none;
  padding: 15px 20px;
  border-radius: 10px;
  cursor: pointer;
  text-decoration: none;
  font-size: 25px;
  /* font-family: inherit; */
  width: 57%;

}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.link{
  color:#36A7F9;
  text-decoration: underline;
}

.add-form {
  margin-bottom: 40px;
}

.form-control{
  position:relative;
     width: 75%; 
  margin:auto;
}
.show-password-btn{
  margin-left : -50px;
  cursor: pointer;
  position: absolute;
  right: 9px;
  bottom: 9px;
  width: 25px;
  height: 25px;
  border: none;
  /* background: url(https://img.icons8.com/material-outlined/24/000000/visible--v2.png) */

}
.form-control input {
  width: 100%;
  height: 43px;
  margin-top: 17px;
  padding: 3px 7px;
  font-size: 17px;
  background-color: rgb(245, 245, 245);
  border-style:solid;
  border-radius: 10px;
  border-width: 0.5px;
  border-color: rgb(226, 226, 226);
}
.form-control input:focus{
  outline:none;
}

.form-control-check {
  width: 57%;
  height: 3rem;
  margin: auto;
}

.form-control-check label {
